.TodoItem-loading {
    background-color: #fafafa6c;
    border-radius: 10px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    width: 50%;
    min-width: 300px;
    height: 80px;
    margin: 25px auto; 
   box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);;
}

.LoaderBalls {
  width: 90px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.LoaderBalls__item {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-image: linear-gradient(to right, #d0cae6 0%, #afa8d6 51%, #d0cae6 100%);
}
.LoaderBalls__item:nth-child(2) {
  animation: opacitychange 1s ease-in-out infinite;
}

.LoaderBalls__item:nth-child(3) {
  animation: opacitychange 1s ease-in-out 0.33s infinite;
}

.LoaderBalls__item:nth-child(1) {
  animation: opacitychange 1s ease-in-out 0.66s infinite
}

@keyframes opacitychange {
  0%, 100% {
    opacity: 0;
}
60% {
    opacity: 1;
}
}